.App {
  text-align: center;
}
*, *:after, *:before{box-sizing: border-box;}
a{text-decoration: none; color: inherit;}

.header{height: 150px;
  line-height: 150px;}
  .header h1 a{color: #000;}

#menu{width: 100%; height: 50px;
  background: #131313;}
.menu{width: 80%;
  margin: 0 auto;
  line-height: 50px;
  display: flex;}
  .menu li{width: 20%;
    text-align: center;}
  .menu li a{color: #fff;
    font-size: 0.9rem;}

.menubox{width: 100%;
  position: absolute; left: 0; top: 200px;
  padding-bottom: 1rem;
  display: flex;
  background: #131313;
  display: none;}
.box{width: 80%;
  margin: 0 auto;
  display: flex;}
  .box ul{width: 20%;}
  .box ul li{height: 30px;
    line-height: 30px;}
  .box ul li a{color: #fff;
    font-size: 0.9rem;}

#main{width: 100%;}
  #main img{width: 100%;}

#plist{width: 80%;
  margin: 4rem auto;}
  #plist h2{margin-bottom: 1rem;}
#list{display: flex;}
  #list li{width: 20%;
    padding-right: 3px;}
  #list li img{width: 100%;}

#footer{width: 100%;
  padding: 10rem 0;
  background: #131313;}
  #footer p{color: #fff;
    font-size: 1.2rem;
    font-weight: bold;
    letter-spacing: 2px;}